
import Header from "@/components/common/Header";
import Address from "@/components/common/Address";
import CheckList from "@/components/common/CheckList";
import { updateAddress } from "@/servers/api";
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "添加地址",
showLeftBack: true,
showRightMore: false
},
maskValueName: "",
maskValuePhone: "",
maskValueDetails: "",
maskValueAddress: []
};
},

components: {
Header,
CheckList,
XButton,
XInput,
Group,
Address
},

computed: {
conHei() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {},

methods: {
formateData() {
if (!this.maskValueName) {
this.showTip("请输入收货人姓名");
return;
} else if (!this.maskValuePhone) {
this.showTip("请输入收货人手机号");
return;
} else if (!this.maskValueAddress.length) {
this.showTip("请选择地区");
return;
} else if (!this.maskValueDetails) {
this.showTip("请输入详细地址");
return;
} else {
this.updateAddressData();
}
},
updateAddressData() {
updateAddress({
tel: this.maskValuePhone,
realname: this.maskValueName,
mobile: this.maskValuePhone,
province: this.maskValueAddress[0],
city: this.maskValueAddress[1],
area: this.maskValueAddress[2],
address: this.maskValueDetails
})
.then(res => {
console.log(res);
if (res.result === 1) {
this.$vux.toast.show({
type: "success",
text: "添加成功",
time: 1000,
isShowMask: true,
onHide: () => {
this.$router.goBack();
}
});
}
})
.catch(err => {
console.log(err);
});
},
getSelAddress(val) {
this.maskValueAddress = val;
},
showTip(conttentTip) {
this.$vux.toast.text(conttentTip, "middle");
setTimeout(() => {
this.$vux.toast.hide();
}, 1000);
}
},

watch: {}
};



